.page__body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.page__hand {
  z-index: 2;
  background-image: url("IMG_5170.10558c02.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  width: 180vmin;
  height: 180vmin;
  position: absolute;
  transform: translate(5.5vmin, 38.5vmin);
}

.stopWatch {
  z-index: 1;
  box-sizing: border-box;
  background-image: url("ciferblat1.9dc3d80f.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 42.3vmin;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.stopWatch__mini {
  z-index: 3;
  background-image: url("cif.a44eeaf7.png");
  background-position: center;
  background-size: cover;
  width: 8vmin;
  height: 8vmin;
  position: absolute;
  transform: translate(36vmin, 28vmin);
}

.stopWatch__hand {
  position: absolute;
}

.stopWatch__hand--second {
  background-color: #313531;
  border-radius: 20%;
  width: .3vmin;
  height: 25%;
  animation: 60s linear infinite rotate-second;
  top: 28.5%;
  left: 49.9%;
}

@keyframes rotate-mini {
  0% {
    transform-origin: 50% 100%;
    transform: rotate(0);
  }

  100% {
    transform-origin: 50% 100%;
    transform: rotate(360deg);
  }
}

@keyframes rotate-second {
  0% {
    transform-origin: 50% 85%;
    transform: rotate(0);
  }

  100% {
    transform-origin: 50% 85%;
    transform: rotate(360deg);
  }
}

.stopWatch__hand--minute {
  z-index: 2;
  background-color: #313531;
  border-radius: 20%;
  width: .4vmin;
  height: 3.5%;
  animation: 3600s linear infinite rotate-mini;
  top: 36.5%;
  left: 50%;
}

.stopWatch__hand--hour {
  background-color: #9e4040;
  border-radius: 20%;
  width: .5vmin;
  height: 2.5%;
  animation: 126000s linear infinite rotate-mini;
  top: 37.5%;
  left: 50%;
}

